<template>
  <div style="margin:0 auto;overflow:auto">
    <el-timeline
      v-infinite-scroll="load"
      infinite-scroll-disabled="disabled"
    >

      <el-timeline-item
        :timestamp="item.timestamp"
        placement="top"
        v-for="(item,index) in data"
        :key="index"
      >
        <el-card>
          <story-post :name="item.basename"></story-post>
        </el-card>
      </el-timeline-item>
      <p v-if="loading">加载中...</p>
      <p v-if="noMore">没有更多了</p>
    </el-timeline>
  </div>
</template>
<script>
import axios from "axios";
import storyPost from "@/components/story_post";
export default {
  components: { storyPost },
  data() {
    return {
      now: 1,
      total: 0,
      data: [],
      loading: false,
    };
  },
  computed: {
    noMore() {
      return this.now >= this.total;
    },
    disabled() {
      return this.loading || this.total;
    },
  },
  methods: {
    init() {
      const url =
        this.$store.state.base_url + `/story/data/page_${this.now}.json`;
      axios.get(url).then((response) => {
        this.data.push(...response.data);
      });
      this.now += 1;
    },
    load() {
      this.loading = true;
      const url =
        this.$store.state.base_url + `/story/data/page_${this.now}.json`;
      axios.get(url).then((response) => {
        this.data.push(...response.data);
        this.now += 1;

        this.loading = false;
      });
    },
    get_all() {
      const url = this.$store.state.base_url + "/story/data/total_number.txt";
      axios.get(url).then((response) => {
        this.total = parseInt(response.data, 10);
      });
    },
  },
  created() {
    this.get_all();
  },
  mounted() {
    this.init();
  },
};
</script>
